import qs from 'query-string'
import config from '../configs'

const urlPrefix = config.domain + config.apiPath

function filterJSON (res) {
  res = res.json()
  if (typeof res === 'string') {
    res = JSON.parse(res)
  }
  return res
}

function filterStatus (res) {
  if (res.status >= 200 && res.status < 300) {
    return res
  } else {
    let error = new Error(res.statusText)
    error.res = res
    error.type = 'http'
    throw error
  }
}

export function get (url, params, token = '') {
  url = urlPrefix + url
  if (params) {
    url += '?' + qs.stringify(params)
  }
  /*
  if (__DEV__) {
    console.info('GET', url)
    console.info('Params', params)
  }
  */
  return fetch(url, { headers: { 'Authorization': token } }).then(filterStatus).then(filterJSON)
}

function _post (url, method, body = {}, token = '') {
  url = urlPrefix + url
  /*
  if (__DEV__) {
    console.info('POST:', url)
    console.info('Body', body)
  }
  */
  return fetch(url, {
    method: method,
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': token
    },
    body: JSON.stringify(body)
  })
  .then(filterStatus)
  .then(filterJSON)
}

export function post (url, body, token = '') {
  return _post(url, 'POST', body, token)
}

export function put (url, body, token = '') {
  return _post(url, 'PUT', body, token)
}

export function patch (url, body, token = '') {
  return _post(url, 'PATCH', body, token)
}

export function del (url) {
  return _post(url, 'DELETE')
}



